// --------------------------- apple fonts ---------------------------

@font-face {
  font-family: Semibold;
  src: url(./../assets/Semibold.otf);
  font-weight: bold;
}

@font-face {
  font-family: Bold;
  src: url(./../assets/Bold.otf);
  font-weight: bold;
}

@font-face {
  font-family: Regular;
  src: url(./../assets/Regular.otf);
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: RegularItalic;
  src: url(./../assets/RegularItalic.otf);
  font-weight: normal;
  font-style: italic;
}

// --------------------------- mixins ---------------------------

@mixin transform($property) {
  -webkit-transform: $property;
  -moz-transform: $property;
  -o-transform: $property;
  -ms-transform: $property;
  transform: $property;
}

@mixin fullHeightWithoutBottomInset {
  height: 100vh;
  min-height: 100vh;

  @supports (height: -webkit-fill-available) {
    height: -webkit-fill-available;
    min-height: -webkit-fill-available;
  }
}

// --------------------------- global ---------------------------

:root {
  --main-light-mode-color: #f1f5f7;
  --main-light-mode-color-rgba: rgba(241, 245, 247, 1);

  --main-dark-mode-color: #1a191e;
  --main-dark-mode-color-rgba: rgba(26, 25, 30, 1);

  --light-mode-text-color: #000000;
  --dark-mode-text-color: #ffffff;

  --navbar-items-opacity: 0.8;
  --navbar-items-individual-padding: 15px;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: Regular, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 18px;
  transition: 0.3s;
}

.ultra {
  transition: 0.3s;
  width: 100vw;
  height: 100vh;
  display: block;
}

// --------------------------- light mode ---------------------------

.light-mode {
  background-color: var(--main-light-mode-color);

  .navbar {
    background-color: var(--main-light-mode-color-rgba);
  }

  h2,
  h3,
  .navbar-name,
  .navbar-mode,
  .MuiFormLabel-root,
  .MuiFormLabel-root.Mui-focused,
  .MuiInputBase-input {
    color: var(--light-mode-text-color);
  }

  .MuiOutlinedInput-notchedOutline {
    border-color: var(--light-mode-text-color) !important;
  }

  .function-header {
    span {
      color: seagreen;
    }
  }
}

// --------------------------- dark mode ---------------------------

.dark-mode {
  background-color: var(--main-dark-mode-color);

  .navbar {
    background-color: var(--main-dark-mode-color-rgba);
  }

  h2,
  h3,
  .navbar-name,
  .navbar-mode,
  .MuiFormLabel-root,
  .MuiFormLabel-root.Mui-focused,
  .MuiInputBase-input {
    color: var(--dark-mode-text-color);
  }

  .MuiOutlinedInput-notchedOutline {
    border-color: var(--main-light-mode-color-rgba) !important;
  }

  .function-header {
    span {
      color: lightcoral;
    }
  }
}

// --------------------------- navbar styling ---------------------------

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 90px;
  z-index: 2000;
  display: flex;
  transition: 0.3s;
}

.navbar-items {
  width: 100%;
  max-width: 1100px;
  margin: 0 auto;
  padding: 0px 15px;
  display: flex;
  align-items: center;
}

.navbar-name {
  display: flex;
  align-items: center;
  white-space: nowrap;
  font-size: 18px;
  font-family: Bold, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  padding: var(--navbar-items-individual-padding);
  opacity: var(--navbar-items-opacity);
  transition: 0.3s;
}

.navbar-space {
  flex-grow: 1;
}

.navbar-mode {
  opacity: var(--navbar-items-opacity);
  padding: var(--navbar-items-individual-padding);
}

.navbar-name:hover,
.navbar-mode:hover {
  cursor: pointer;
  opacity: 1;
}

// --------------------------- text styling ---------------------------

h2,
h3 {
  transition: 0.3s;
}

ul {
  padding: 0;
  padding-top: 5px;
}

li {
  list-style-type: none;
  margin: 0px 0px 10px 0px;
  display: flex;
  align-items: center;
}

// --------------------------- container styling ---------------------------

.navbar-offset-space {
  height: 90px;
  width: 100%;
}

.divider {
  width: 100%;
  height: 1px;
  margin-bottom: 30px;
  background-color: #c7c7c7;
}

.function-list-container {
  height: 90%;
  width: 100%;
  display: flex;
}

.function-list {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0px 30px;
  display: flex;
  flex-wrap: wrap;
}

.function-card {
  width: 100%;
  padding-top: 30px;
}

.function-header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  span {
    transition: 0.3s;
  }
}

.form-label {
  display: flex;
  width: 40%;
  max-width: 200px;

  h3 {
    text-align: left;
    margin-right: 20px;
    width: 50%;
  }
}

.function-name-field,
.function-regular-field,
.function-edge-field {
  display: flex;
  margin: 10px;
}

.form-icons {
  padding-left: 20px;

  :hover {
    cursor: pointer;
  }
}

.footer {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

// --------------------------- input styling ---------------------------

.MuiOutlinedInput-notchedOutline,
.MuiFormLabel-root,
.MuiFormLabel-root.Mui-focused,
.MuiInputBase-input {
  transition: 0.3s;
}

.MuiFormLabel-root,
.MuiFormLabel-root.Mui-focused {
  opacity: 0.6;
}

// --------------------------- keyframe animations ---------------------------

@keyframes forceReflow {
  from {
    transform: scale(0.999999);
  }
  to {
    transform: scale(1);
  }
}
